<template>
  <div class="view container">
    <h1 class="mt-5 mb-3 title">Prevenção de Fraudes</h1>
    <h2 class="mt-4 mb-3 sub-title">Práticas Empresariais</h2>
    <ul>
      <li class="mb-4">
        Implemente sistemas seguros para armazenamento de dados. Isso inclui
        criptografia, firewalls e outras ferramentas para garantir a segurança
        das informações.
      </li>
      <li class="mb-4">
        Adote ferramentas que observam sinais de alerta como: diferentes
        endereços de cobrança e entrega, vários pedidos para uma pessoa por meio
        de cartões diferentes, vários pedidos em grandes quantidades pagos
        usando o mesmo cartão ou um aumento repentino no volume de pedidos.
      </li>
      <li class="mb-4">
        Forneça aos clientes conteúdos sobre como se proteger de fraudes.
        Conscientizá-los inclui ensinar sobre phishing, como identificar sites e
        links suspeitos e como manter suas informações pessoais seguras.
      </li>
    </ul>
    <h2 class="mt-4 mb-3 sub-title">Práticas para Clientes</h2>
    <ul>
      <li class="mb-4">
        Tome cuidado com as informações que você publica na internet. Nunca
        informe o número do seu cartão, data de validade ou código de segurança
        para sites, telefones e emails que você não conhece e confia.
      </li>
      <li class="mb-4">
        Utilize senhas longas e mantenha-as seguras. Sua senha deve ter pelo
        menos 12 caracteres, incluir letras maiúsculas e minúsculas, números e
        caracteres especiais.
      </li>
      <li class="mb-4">
        Tome cuidado quando estiver utilizando um Wi-Fi público. Redes públicas
        nem sempre são seguras, então é mais prudente evitá-las para fazer
        transações bancárias. Se você não tiver escolha, utilize uma VPN para
        tornar sua conexão mais segura.
      </li>
      <li class="mb-4">
        Veja seu extrato bancário regularmente. Se encontrar alguma transação
        suspeita, avise seu banco imediatamente.
      </li>
      <li class="mb-4">
        Fique atento a e-mails e mensagens suspeitas (phishing). Essas mensagens
        aparentam ser enviadas por empresas legítimas, mas são feitas por
        criminosos tentando obter informações pessoais. Nunca clique em links
        enviados por remetentes desconhecidos e nunca forneça informações
        sensíveis para desconhecidos.
      </li>
      <li class="mb-4">
        Mantenha seus aplicativos, celulares e computadores atualizados. Falhas
        de segurança já corrigidas em versões antigas podem permitir que sua
        conta seja invadida por hackers.
      </li>
    </ul>
  </div>
</template>

